import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core';
import { Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-baby-alert',
  templateUrl: './baby-alert.component.html',
  styleUrls: ['./baby-alert.component.css']
})
export class BabyAlertComponent implements OnInit {

  @Input() valueFromParent : String = 'nothing so far';
  @Output() throwThis = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }
  emitMe() : void {
    this.throwThis.emit();
  }
}
