import { Component, OnInit } from '@angular/core';
import {AlertingService} from '../../services/alerting.service'

@Component({
  selector: 'app-hello-world',
  templateUrl: './hello-world.component.html',
  styleUrls: ['./hello-world.component.css']
})
export class HelloWorldComponent implements OnInit {

  messageFromComponent : String = 'hello owrld';
  myId : String = 'randomId';
  bgColor : String = 'green';
  showMe : boolean = true;
  todoValue : String = 'type something'
  items : Array<String> = [];
  constructor(private serv : AlertingService) {
    this.messageFromComponent = 'message from outer world';
  }

  ngOnInit(): void {
  }

  divClick() : void {
    this.serv.show('I am a special div, green !!');
    this.showMe = !this.showMe;
  }

  addMe() : void{
    this.items.push(this.todoValue);
  }
  onChildEv() : void {
    this.serv.show('I am a comng from child comp !!');
  }
}
